*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

body {
  background: linear-gradient(109.6deg, rgb(36, 45, 57) 11.2%, rgb(16, 37, 60) 51.2%, rgb(0, 0, 0) 98.6%);
  font-family: sans-serif;
  font-size: 1.6rem;
}

.app {
  min-height: 100vh;
  position: relative;
}
.app__wrapper {
  width: 96%;
  max-width: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 3em 2em;
  color: #e3e3e3;
  text-align: center;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  border: 2px solid rgba(255, 255, 255, 0.18);
  border-radius: 8px;
  box-shadow: 0 8px 32px 0 #151515;
  transform: translate(-50%, -50%);
}
.app__header {
  margin-bottom: 2em;
}
.app__header-logo {
  margin-bottom: 1.5em;
}
.app__body-container {
  margin-bottom: 1.5em;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.app__body-container:last-child {
  margin-bottom: 0;
}
.app__body-label, .app__body-input, .app__body-select {
  font-size: 1.8rem;
}
.app__body-label {
  margin-bottom: 0.5em;
}
.app__body-select, .app__body-swap-button {
  cursor: pointer;
}
.app__body-input, .app__body-select {
  width: 100%;
  padding: 1em;
  border-radius: 8px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.075));
  color: #e3e3e3;
  border: 2px solid #e3e3e3;
}
.app__body-swap-button {
  width: 60px;
  height: 60px;
  margin: 3em auto 1em;
  padding-top: 1em;
  padding-bottom: 0.8em;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.075));
  border: 2px solid #e3e3e3;
  border-radius: 50%;
}
.app__body-swap-button-icon {
  width: 30px;
}
.app__body-convert-button, .app__body-result {
  width: 100%;
  padding: 1em;
  border-radius: 8px;
  background: #e3e3e3;
  color: #151515;
  font-size: 2rem;
  line-height: 1;
}
.app__body-convert-button {
  cursor: pointer;
}
.app__body-result {
  padding: 1.5em;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
  color: #e3e3e3;
}
.app__footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2em;
  text-align: center;
}